import { createContext, useState, useEffect } from "react";
import { useSnackbar } from 'notistack';
import Localbase from 'localbase'
import config from "../config";

export const StateContext = createContext();

export default function StateContextProvider({ children }) {
    const [user, setUser] = useState(JSON.parse(localStorage.getItem("user")) || null)
    const setUsers = () => setUser(JSON.parse(localStorage.getItem("user")) || null)
    const [token, setToken] = useState(localStorage.getItem("token") || null)
    const setTokens = () => setToken(localStorage.getItem("token"))
    const [beneList, setbeneList] = useState([])
    const [object, setObject] = useState({})
   
    let db = new Localbase('list_db').collection('list')
    const { enqueueSnackbar } = useSnackbar();
    const notification = (message, type = "info",) => {
        enqueueSnackbar(message, {
            variant: type,
            anchorOrigin: { vertical: "top", horizontal: "right" }

        });

    }
    const Login = (data) => {
        console.log(data)
        fetch(`${config.endPoint}/user/login`, {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify(data),
        })
            .then((res) => {
                return res.json()
            })
            .then((response) => {
                if (response.status == "success") {
                    localStorage.setItem("user", JSON.stringify(response.user))
                    setUser(JSON.parse(localStorage.getItem("user")))
                    localStorage.setItem("token", JSON.stringify(response.token))
                    setToken(JSON.parse(localStorage.getItem("token")))
                    notification("Login Success", "success")
                    return
                }
                notification(response.message, "error")
            }).catch((err) => {
                notification(err.message, "error")
            });
    };
    function fetchBene() {
        fetch(`${config.endPoint}/beneficiaries`).
            then(res => res.json()).
            then(arr => {
             let list = arr.map(obj=>{
              return { id:obj._id,
                       name: obj.fullName,
                       age: obj.age,
                       gender: obj.gender,
                       maritalStatus: obj.maritalStatus,
                       state: obj.state,
                       lga: obj.lga,
                       ward: obj.ward,
                       phone: obj.phone,
                       status: obj.status,}
                })
                setbeneList([...list])
               
                // arr.forEach(obj => {
                //     return db.add({
                //         id:obj._id,
                //         name: obj.fullName,
                //         age: obj.age,
                //         gender: obj.gender,
                //         maritalStatus: obj.maritalStatus,
                //         state: obj.state,
                //         lga: obj.lga,
                //         ward: obj.ward,
                //         phone: obj.phone,
                //         status: obj.status,
                //     })
                // })
            })
    }
    useEffect(() => {
        fetchBene()
    }, [])
    const context = {
        notification,
        Login,
        beneList,
        token,
        user,

    }
    return <StateContext.Provider
        value={context}>
        {children}
    </StateContext.Provider>;
}